.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;

}

.home-image{
    width: 100%;
    border-radius: 25px;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}
.home-row{
    display: flex;
    z-index: 1;
    margin-right: 10px;
    margin-left: 10px;
}